:root {
    /* primary */
    --button-inverted-primary-base-bg-color: var(--color-light-bg-accent);
    --button-inverted-primary-hover-bg-color: var(--color-light-bg-accent-tint-15);
    --button-inverted-primary-active-bg-color: var(--color-light-bg-accent-tint-20);
    --button-inverted-primary-disabled-bg-color: var(
        --color-light-specialbg-secondary-transparent-inverted
    );
    --button-inverted-primary-disabled-color: var(--color-light-text-tertiary-inverted);
    --button-inverted-primary-color: var(--color-static-text-primary-light);

    /* secondary */
    --button-inverted-secondary-base-bg-color: var(
        --color-light-specialbg-tertiary-transparent-inverted
    );
    --button-inverted-secondary-hover-bg-color: var(
        --color-light-specialbg-tertiary-transparent-inverted-tint-15
    );
    --button-inverted-secondary-active-bg-color: var(
        --color-light-specialbg-tertiary-transparent-inverted-tint-20
    );
    --button-inverted-secondary-disabled-bg-color: var(
        --color-light-specialbg-secondary-transparent-inverted
    );
    --button-inverted-secondary-base-border-color: transparent;
    --button-inverted-secondary-disabled-border-color: transparent;
    --button-inverted-secondary-color: var(--color-light-text-primary-inverted);
    --button-inverted-secondary-disabled-color: var(--color-light-text-tertiary-inverted);

    /* tertiary */
    --button-inverted-tertiary-base-bg-color: transparent;
    --button-inverted-tertiary-hover-bg-color: var(--color-light-bg-primary-alpha-15);
    --button-inverted-tertiary-active-bg-color: var(--color-light-bg-primary-alpha-20);
    --button-inverted-tertiary-hover-border-color: var(--color-light-border-underline);
    --button-inverted-tertiary-disabled-bg-color: transparent;
    --button-inverted-tertiary-base-border-color: var(--color-light-border-underline);
    --button-inverted-tertiary-disabled-border-color: var(--color-light-graphic-tertiary-inverted);
    --button-inverted-tertiary-color: var(--color-light-text-primary-inverted);
    --button-inverted-tertiary-disabled-color: var(--color-light-text-tertiary-inverted);

    /* outlined */
    --button-inverted-outlined-base-bg-color: transparent;
    --button-inverted-outlined-hover-bg-color: rgba(255, 255, 255, 0.15);
    --button-inverted-outlined-active-bg-color: rgba(0, 0, 0, 0.15);
    --button-inverted-outlined-disabled-bg-color: transparent;
    --button-inverted-outlined-base-border-color: var(--color-light-border-underline-inverted);
    --button-inverted-outlined-disabled-border-color: rgba(35, 53, 73, 0.3);
    --button-inverted-outlined-color: var(--color-dark-text-primary);
    --button-inverted-outlined-disabled-color: rgba(255, 255, 255, 0.3);

    /* ghost */
    --button-inverted-ghost-base-color: var(--color-light-text-primary-inverted);
    --button-inverted-ghost-hover-color: var(--color-light-text-primary-inverted-shade-24);
    --button-inverted-ghost-active-color: var(--color-light-text-primary-inverted-shade-40);
    --button-inverted-ghost-disabled-color: var(--color-light-text-tertiary-inverted);

    /* link */
    --button-inverted-link-base-color: var(--color-light-text-primary-inverted);
    --button-inverted-link-hover-bg-color: var(--color-light-bg-primary-alpha-15);
    --button-inverted-link-active-bg-color: var(--color-light-bg-primary-alpha-20);
    --button-inverted-link-disabled-color: var(--color-light-text-tertiary-inverted);

    /* loader */
    --button-spinner-static-color: var(--color-static-graphic-light);
    --button-spinner-inverted-color: var(--color-light-graphic-primary-inverted);
}
.button__primary_1lala {
    color: var(--button-inverted-primary-color);
    background-color: var(--button-inverted-primary-base-bg-color);
    border: 1px solid transparent
}
.button__primary_1lala:hover {
        background-color: var(--button-inverted-primary-hover-bg-color);
    }
.button__primary_1lala:active {
        background-color: var(--button-inverted-primary-active-bg-color);
    }
.button__primary_1lala > .button__loader_1lala {
        color: var(--button-spinner-static-color);
    }
.button__secondary_1lala {
    color: var(--button-inverted-secondary-color);
    background-color: var(--button-inverted-secondary-base-bg-color);
    border: 1px solid var(--button-inverted-secondary-base-border-color)
}
.button__secondary_1lala:hover {
        background-color: var(--button-inverted-secondary-hover-bg-color);
    }
.button__secondary_1lala:active {
        background-color: var(--button-inverted-secondary-active-bg-color);
    }
.button__secondary_1lala > .button__loader_1lala {
        color: var(--button-spinner-inverted-color);
    }
.button__outlined_1lala {
    color: var(--button-inverted-outlined-color);
    background-color: var(--button-inverted-outlined-base-bg-color);
    border: 1px solid var(--button-inverted-outlined-base-border-color)
}
.button__outlined_1lala:hover {
        background-color: var(--button-inverted-outlined-hover-bg-color);
    }
.button__outlined_1lala:active {
        background-color: var(--button-inverted-outlined-active-bg-color);
    }
.button__tertiary_1lala {
    color: var(--button-inverted-tertiary-color);
    background-color: var(--button-inverted-tertiary-base-bg-color);
    border: 1px solid var(--button-inverted-tertiary-base-border-color)
}
.button__tertiary_1lala:hover {
        background-color: var(--button-inverted-tertiary-hover-bg-color);
        border-color: var(--button-inverted-tertiary-hover-border-color);
    }
.button__tertiary_1lala:active {
        background-color: var(--button-inverted-tertiary-active-bg-color);
    }
.button__tertiary_1lala > .button__loader_1lala {
        color: var(--button-spinner-inverted-color);
    }
.button__filled_1lala {
    color: var(--color-dark-text-primary);
    background-color: var(--color-dark-bg-quaternary)
}
.button__filled_1lala:hover {
        background-color: rgb(99, 112, 126);
    }
.button__filled_1lala:active {
        background-color: rgb(61, 74, 88);
    }
.button__transparent_1lala {
    color: var(--color-light-text-primary-inverted);
    background-color: rgba(255, 255, 255, 0.1)
}
.button__transparent_1lala:hover {
        background-color: rgba(255, 255, 255, 0.2);
    }
.button__transparent_1lala:active {
        /* TODO: какой цвет? */
        background-color: rgba(255, 255, 255, 0.05);
    }
.button__link_1lala {
    color: var(--button-inverted-link-base-color);
    background-color: transparent;
    border: 1px solid transparent
}
.button__link_1lala:hover {
        background-color: var(--button-inverted-link-hover-bg-color);
    }
.button__link_1lala:active {
        /* TODO: какой цвет? */
        background-color: var(--button-inverted-link-active-bg-color);
    }
.button__link_1lala > .button__loader_1lala {
        color: var(--button-spinner-inverted-color);
    }
.button__ghost_1lala {
    color: var(--button-inverted-ghost-base-color)
}
.button__ghost_1lala:hover {
        color: var(--button-inverted-ghost-hover-color);
    }
.button__ghost_1lala:active {
        color: var(--button-inverted-ghost-active-color);
    }
.button__ghost_1lala > .button__loader_1lala {
        color: var(--button-spinner-inverted-color);
    }
.button__component_1lala:disabled.button__primary_1lala {
        color: var(--button-inverted-primary-disabled-color);
        background-color: var(--button-inverted-primary-disabled-bg-color)
    }
.button__component_1lala:disabled.button__primary_1lala.button__loading_1lala {
            background-color: var(--button-inverted-primary-base-bg-color);
        }
.button__component_1lala:disabled.button__secondary_1lala {
        color: var(--button-inverted-secondary-disabled-color);
        background-color: var(--button-inverted-secondary-disabled-bg-color);
        border-color: var(--button-inverted-secondary-disabled-border-color)
    }
.button__component_1lala:disabled.button__secondary_1lala.button__loading_1lala {
            color: var(--button-inverted-secondary-color);
            background-color: var(--button-inverted-secondary-base-bg-color);
            border-color: var(--button-inverted-secondary-base-border-color);
        }
.button__component_1lala:disabled.button__outlined_1lala {
        color: var(--button-inverted-outlined-disabled-color);
        background-color: var(--button-inverted-outlined-disabled-bg-color);
        border-color: var(--button-inverted-outlined-disabled-border-color)
    }
.button__component_1lala:disabled.button__outlined_1lala.button__loading_1lala {
            color: var(--button-inverted-outlined-color);
        }
.button__component_1lala:disabled.button__tertiary_1lala {
        color: var(--button-inverted-tertiary-disabled-color);
        background-color: var(--button-inverted-tertiary-disabled-bg-color);
        border-color: var(--button-inverted-tertiary-disabled-border-color)
    }
.button__component_1lala:disabled.button__tertiary_1lala.button__loading_1lala {
            color: var(--button-inverted-tertiary-color);
        }
.button__component_1lala:disabled.button__filled_1lala {
        color: rgba(255, 255, 255, 0.3);
        background-color: rgba(72, 87, 103, 0.3);
        border-color: transparent
    }
.button__component_1lala:disabled.button__filled_1lala.button__loading_1lala {
            color: var(--color-dark-text-primary);
            background-color: var(--color-dark-bg-quaternary);
            border-color: transparent;
        }
.button__component_1lala:disabled.button__transparent_1lala {
        color: rgba(255, 255, 255, 0.3);
        background-color: rgba(255, 255, 255, 0.03)
    }
.button__component_1lala:disabled.button__transparent_1lala.button__loading_1lala {
            color: var(--color-light-text-primary-inverted);
            background-color: rgba(255, 255, 255, 0.1);
        }
.button__component_1lala:disabled.button__link_1lala {
        color: var(--button-inverted-link-disabled-color);
        background-color: transparent
    }
.button__component_1lala:disabled.button__link_1lala.button__loading_1lala {
